:root { 
  --c01:#ffffff;
  --c02:#f5f3f4;
  --c03:#d3d3d3;
  --c04:#b1a7a6;
  --c05:#e5383b;
  --c06:#ba181b;
  --c07:#a4161a;
  --c08:#660708;
  --c09:#161a1d;
  --c10:#0b090a;
}


body{ 
  background-color: var(--c1);
  color: #0b090a;
  color: var(--c10);
  padding: 50px 0px 120px;
  font-family: 'Montserrat', sans-serif;
  /* font: 16px 'Roboto', sans-serif, Helvetica, Arial, sans-serif; */
  font-size: 16px;
  font-weight: 400;
  width: 100vW;
  text-align: center ;  
}

a{
  text-decoration: none;
  color:black;
  
}

.btnsetting {
  background: none;
color: inherit;
border: none;
padding: 0;
font: inherit;
cursor: pointer;
outline: inherit;
}
.hr-big {
  height: 12px;
  border: 0;
  box-shadow: inset 0 12px 12px -12px rgba(0, 0, 0, 0.5);
}

