.itemrow {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;

}

.item-editor a{
  color: #ba181b;
  color: var(--c06);
}
.leftrow {
  text-align: left;
}

.rightrow {
  text-align: right;
}
.navbar-top{
    z-index: 1;
    /* background-image: url(./../icons/headerv2.png); */
    background-size: cover;
    background-position-x: center;
    position: fixed; /*cambiar en res 500 y 1000. y ponerla hidden en 1000*/
    width: 100%;
    height: auto;
    top: 0px;
    left: 0px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border-bottom: solid rgb(163, 163, 163) 0.3px;
  }
   
   :root { 
    --c01:#ffffff;
    --c02:#f5f3f4;
    --c03:#d3d3d3;
    --c04:#b1a7a6;
    --c05:#e5383b;
    --c06:#ba181b;
    --c07:#a4161a;
    --c08:#660708;
    --c09:#161a1d;
    --c10:#0b090a;
  } 


  .navbar-bottom {
    z-index: 1;
    background-color: #0b090a;
    background-color: var(--c10);
    position: fixed; /*cambiar en res 500 y 1000. y ponerla hidden en 1000*/
    width: 100%;
    bottom: 0;
    overflow: hidden;
    left: 0px;
    border-top: solid rgb(163, 163, 163) 0.3px;
  }
  
  .navbar-bottom ul {
    text-align: center;
    padding: 0;
    margin: 0;
  }
  
  .navbar-bottom li {
    display: inline-block;
    padding: .2rem 3%;
    
  }
  .navbar-bottom a {    
    -webkit-text-decoration: yellow;    
            text-decoration: yellow; 
    color: #f5f3f4; 
    color: var(--c02);
    font-size: 11px;
    font-weight: 100;

  }
  
  .navbar-texticon {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .navbar-icon{
    background: 50% 50% no-repeat; 
    height: 10vh;
    min-height: 25px;
    max-height: 40px;
    width: 10vh;
    min-width: 25px;
    max-width: 40px;
    border-radius: 50%;
    padding: 0.3vh;
    
  }

  /* #nav-avatar {
    height: 35px;    
  } */
  .navbar-icon-clicked{
 
    height: 10vh;
    min-height: 25px;
    max-height: 40px;
    border-radius: 25%;
    background-color: #EFFAFB;
    padding: 0.3vh;
    border: solid #36B8BF 1px;
  }

  #iclogo {
    height: 7vh;
  }

  
  
